body {
    margin: 0;
    font-family: Arial, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f5f5f5;
    overflow: hidden;
}

.app {
    width: 100%;
    height: 100%;
    position: relative;
}

.start-screen, .end-screen {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
}

.start-screen a, .end-screen a {
    font-size: 2rem;
    color: #333;
    text-decoration: none;
    border: 2px solid #333;
    padding: 10px 20px;
    border-radius: 5px;
}

.character-container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    overflow: hidden;
}

.character {
    font-size: 10rem;
    font-weight: bold;
    transition: transform 0.5s ease-in-out;
}

.pronunciation {
    font-size: 4rem;
    font-weight: normal;
    color: gray;
    transform: translateY(100%);
    transition: transform 0.5s ease-in-out;
}

.character.show + .pronunciation {
    transform: translateY(0);
}

.pronunciation.show {
    transform: translateY(-100%);
}
